import Stomp from "stompjs";
import { projectconfig } from "../../static/projectconfig";
// ============================一般使用的变量============================
let number = 0;
let xStompClient = null;
export const dataAdapterInit = initialize;
export const calcSend = send;
// ============================  大函数体   ============================
function initialize()
{
  xStompClient = null;
  var url = projectconfig.stompHost;

  var ws= new WebSocket(url);


  xStompClient = Stomp.over(ws);
  //xStompClient.debug = null;
  xStompClient.connect({}, () => connectCallBackSubscribe(xStompClient), error => reconnect(error, xStompClient));
}
// 断线重连
function reconnect(error, xStompClient)
{
  //连接失败时再次调用函数
  number++;
  xStompClient.connected = false;


  console.log(error)
  clearTimeout(setTimeout(initialize(), 1000 * 5));
  debugX("DataAdapter reconnect:" + number + " 次");
  return;
}
// ============================  订阅函数体  ============================
function connectCallBackSubscribe(xStompClient)
{
  number = 0;
  xStompClient.connected = true;
  // xStompClient.subscribe(projectconfig.SubscribeMap.basicInformation, stompMessage => reflexWindturbineBasicInformation(stompMessage));
  // xStompClient.subscribe(projectconfig.SubscribeMap.cmdInformation, stompMessage => reflexWindturbineBasicInformation(stompMessage));
  // xStompClient.subscribe(projectconfig.SubscribeMap.cmdInformation, stompMessage => reflexWindturbineBasicInformation(stompMessage));


  xStompClient.subscribe("/topic/cmd", stompMessage => reflexWindturbineBasicInformation(stompMessage));
  xStompClient.subscribe("/application/control", stompMessage => reflexappcmd(stompMessage));
  xStompClient.subscribe("/application/cmd", stompMessage => reflexappcmd(stompMessage));

}
// ============================  解析函数体  ============================
function reflexWindturbineBasicInformation(stompMessage)
{

  stompMessage;
  //console.log(stompMessage);
  //var windturbineInformationArray = JSON.parse(stompMessage.body);
  //store.dispatch('updateWindturbineArray', windturbineInformationArray);
}

function reflexappcmd(stompMessage)
{
  console.log(stompMessage)  ;
}

// ============================  大函数体   ============================
/*** 发送数据 
 * @param {string} destination 地址,从配置文件中找
 * @param {{}} headers 头信息,没有的信息函数自动添加
 * @param {string} body 数据内容
 */
function send(destination, headers, body)
{
  console.log("Send");
  // 自定义头信息
  //headers["userDefineHeaderA"] = "123";
  if (xStompClient && xStompClient.connected)
  {
    xStompClient.send(destination, headers, body);
  }
}

// ============================  其他  ============================
function debugX(text)
{
  console.log(text);
}